
#app {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  padding: 0px;
  background: url(/static/img/Background1.dc24b45.svg);
  /* background-size: cover; */
  height:100%;
}
body{
  background: linear-gradient(10deg, rgb(139, 185, 255),rgb(255, 107, 188));
  margin: 0px;
  padding: 0px;
}
h1{
  color:white;
  font-weight: 800;
}
h2{
  color:rgba(255, 255, 255, 0.719);
}
a{
  cursor: pointer;
  text-decoration: none;
  color:inherit;
}
button{
  cursor: pointer;
}
@media screen and (max-device-width: 1024px){
body{
    background: linear-gradient(10deg,rgb(139, 185, 255), rgb(225, 107, 255));
}
#app{
    background-position: -400px 500px;
}
}

.header-button{
    background: rgb(0, 255, 183);
    border-radius: 100px;
    color:white;
    border:none;
    font-weight: 600;
    padding: 0.3rem 1rem 0.5rem 1rem;
    font-size: 1.5rem;
}
.header-button-empty{
    background: none;
    border-radius: 100px;
    -webkit-box-shadow: 0 0 0 4px white inset;
            box-shadow: 0 0 0 4px white inset;
    color:white;
    border:none;
    font-weight: 600;
    padding: 0.3rem 1rem 0.5rem 1rem;
    font-size: 1.5rem;
}
.quote{
    padding: 2rem;
    color:white;
    /* font-family: Avenir, Helvetica-Neue; */
    font-weight: 400;
    font-style: normal;
    font-family: medium-content-serif-font,Georgia,Cambria,"Times New Roman",Times,serif;
    font-size: 40px;
    width: 60vw;
    margin: auto;
}
.logo{
    width: 10rem;
}
.title{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: start;
        -ms-flex-align: start;
            align-items: flex-start;
}
.header{
    padding:2rem 0rem;
    margin:0px;
    text-align: center;
}
.headline{
    color:white;
    font-size: 3rem;
    padding: 2rem;
    font-weight: 100;
}
.headline span{
    padding:1rem;
}
.name{
    color: white;
    font-weight: 900;
    font-size: 4rem;
    text-align: left;
    padding-left: 2rem;
}
@media screen and (max-device-width: 1024px){
.headline{
    font-size: 2.5rem;  
    padding: 3rem 0.5rem;
}
.name{
    font-size:3.5rem;
    text-align: center;
    padding:0px;
}
.title{
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.header{
    /* height:50vh; */
}
.projects{
    margin: 0%;
}
.text{
    width: 100vw;
}
.quote{
    padding:1rem;
    width: auto;
}
}


.hunk-white{
  height: 500px;
  background-color:rgba(255, 255, 255, 0.877);
  width:100vw;
  padding: 1rem 0;
  margin-top: 1rem;
}
.hunk-clear{
  height: 500px;
  background-color:none;
  width:100vw;
  padding: 1rem 0;
  margin-top: 1rem;
}


.section{
        background: rgba(255, 255, 255, 0.9);
}
.projects{
        min-height: 500px;
        width:100vw;
        padding: 1rem 0;
        margin: 1rem 0;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: start;
            -ms-flex-pack: start;
                justify-content: flex-start;
        -webkit-box-align: start;
            -ms-flex-align: start;
                align-items: flex-start;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
}
.project{
        position: relative;
        width:25vw;
        height: 25vw;
        overflow: hidden;
}
.project img{
        width:100%;
        -webkit-filter:opacity(0.6);
                filter:opacity(0.6);
        -webkit-transition: -webkit-filter 0.3s ease-in-out;
        transition: -webkit-filter 0.3s ease-in-out;
        transition: filter 0.3s ease-in-out;
        transition: filter 0.3s ease-in-out, -webkit-filter 0.3s ease-in-out;
}
.project img:hover{
        -webkit-filter:opacity(1);
                filter:opacity(1);
}
.text{
        position: absolute;
        bottom: 0px;
        left: 0px;
        width: 25vw;
        text-align: left;
        padding: 0 0.5rem;
}
.text h1, .text h2{
        color:white;
        margin:0.2rem;
        font-size:1rem;
        /* background:white; */
}
.text i{
        font-size:0.8rem;
        color:rgba(255, 255, 255, 0.788);
}
.text h2{
        font-size:0.8rem;
        color:rgba(255, 255, 255, 0.822);
}
.genre-titles{
        text-align: left;
        font-size: 2rem;
        color:rgb(213, 139, 216);
        padding: 1rem 0 0 1rem;
}
.itsalive{
        /* background:black; */
}
@media screen and (max-device-width: 1024px){
.project{
        width: 50vw;
        height: 50vw;
        -webkit-filter:opacity(1);
                filter:opacity(1);
}
.projects{
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
        padding:0px;
}
.genre-titles{
        text-align: left;
        font-size: 2rem;
}
.text{
        font-size:0.1rem;
        width: 100%;
}
.text h1, .text h2{
        font-size: 1rem;
        margin:0.2rem;
        /* background:white; */
}
.text h2{
        font-size:0.8rem;
}
.quote{
        font-size:25px;
        padding:1rem;
}
}
